.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    border-radius: 5px;
    font-size: 1.4rem;
    color: var(--text-color);
    font-weight: 700;
    background-color: var(--while);
    border: 1px solid transparent;
    user-select: none;
    + .wrapper {
        margin-left: 7px;
    }

    &.disabled {
        pointer-events: none;
        opacity: 0.5;
    }
}

.tittle + .icon,
.icon + .tittle {
    margin-left: 8px;
}
.icon {
    display: block;
    width: 20px;
    text-align: left;
}

// Button types
.rounded {
    border-radius: 1307px;
    box-shadow: 0 2px 8px rgba(0 0 0 / 6%);
    border-color: rgba(22, 24, 35, 0.12);

    &:hover {
        box-shadow: 0 2px 8px rgba(0 0 0 / 13%);
        border-color: rgba(22, 24, 35, 0.2);
    }
}
.primary {
    color: var(--while);
    background-color: var(--primary);
    border-color: var(--primary);
    cursor: pointer;
    &:hover {
        border-color: var(--primary);
        background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06), var(--primary));
    }
}
.outline {
    color: var(--primary);
    border-color: currentColor;

    &:hover {
        border-color: currentColor;
        background-color: rgba(255, 44, 85, 0.06);
    }
}
.text {
    &:hover {
        text-decoration: underline;
    }
}

//Button size
.small {
    min-width: 88px;
    padding: 4px 16px;
}
.large {
    padding: 14px 16px;
    min-width: 137px;
}

.leftIcon {
    margin-right: 5px;
}
.rightIcon {
    margin-left: 5px;
}
