.search,
.search-result {
    width: 361px;
}
.search {
    height: 46px;
    padding-left: 16px;
    background-color: rgba(22, 24, 35, 0.06);
    border-radius: var(--search-border-radius);
    display: flex;
    position: relative;
    border: 1.5px solid transparent;

    input {
        flex: 1;
        color: var(--black);
        height: 100%;
        font-size: 1.6rem;
        caret-color: var(--primary);
        background-color: transparent;
    }
    input:not(:placeholder-shown) ~ .search-btn {
        color: rgba(22, 24, 35, 0.75);
    }

    &:focus-within {
        border: 1.5px solid rgba(22, 24, 35, 0.2);
    }

    &::after {
        content: ' ';
        position: absolute;
        height: calc(var(--search-height) - var(--search-top-spacer) * 2);
        top: var(--search-top-spacer);
        right: 52px;
        width: 1px;
        background-color: rgba(22, 24, 35, 0.12);
    }
}

.search-title {
    padding: 5px 12px;
    font-size: 1.4rem;
    font-weight: 600;
    color: rgba(22, 24, 35, 0.5);
}
.clear,
.loading {
    position: absolute;
    font-size: 16px;
    color: rgba(22, 24, 35, 0.35);
    right: calc(var(--search--button-width) + 13px);
    top: 50%;
    transform: translateY(-50%);
}
.loading {
    animation: spinner 1.3s linear infinite;
}
@keyframes spinner {
    from {
        transform: translateY(-50%) 0;
    }
    to {
        transform: translateY(-50%) rotate(360deg);
    }
}

.search-btn {
    color: #828282;
    width: var(--search--button-width);
    height: 100%;
    border-top-right-radius: var(--search-border-radius);
    border-bottom-right-radius: var(--search-border-radius);
    font-size: 2rem;

    &:hover {
        cursor: pointer;
        background-color: rgba(22, 24, 25, 0.06);
    }
    &:active {
        background-color: rgba(22, 24, 25, 0.1);
    }
}
