.wrapper {
    display: flex;
    align-items: center;
    padding: 9px 16px;
    cursor: pointer;
    &:hover {
        background-color: rgba(22, 24, 35, 0.06);
    }
}
.avartar {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.info {
    flex: 1;
    margin-left: 12px;
}
.check {
    margin-left: 6px;
    font-size: 1.3rem;
    color: rgb(32, 213, 236);
}
.name {
    font-size: 1.6rem;
    font-weight: 500;
}

.username {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, 0.5);
}
