.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    width: var(--default-layout-width, 1150px);
    display: flex;
    margin-top: var(--default-layout-header-height);
    padding: 0 var(--default-layout-horizontal-spacer);
}
.content {
    flex: 1;
    background-color: #ccc;
}

