.wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: min((100vh - 96px) - 60px, 734px);
    min-height: 100px;
    background-color: #fff;
    box-shadow: rgb(0 0 0 / 12%) 0 2px 12px;
    border-radius: 8px;
    padding-top: 7px;
    margin-top: -2px;
}
