.wrapper {
    --search-border-radius: 92px;
    --search-height: 46px;
    --search-top-spacer: 9px;
    --search--button-width: 52px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    height: var(--default-layout-header-height);
    width: 100%;
    box-shadow: 0px 1px 1px rgba(0 0 0 / 12%);
    background-color: var(--while);
}
.inner {
    height: 100%;
    width: var(--default-layout-width, 1150px);
    padding: 0 var(--default-layout-horizontal-spacer);
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo {
    cursor: pointer;
    display: flex;
}

.upload-btn {
    border-color: rgba(22, 24, 25, 0.13);
    height: 36px;
    border-radius: 2px;
    margin-right: 13px;

    &:hover {
        border-color: rgba(22, 24, 25, 0.13);
        background-color: rgba(22, 24, 25, 0.02);
    }
}
// Actions
.actions {
    display: flex;
    align-items: center;
}
.more-btn {
    font-size: 2rem;
    margin-left: 28px;
    background: transparent;
    color: var(--text-primary);
    padding: 4px 13px;
    cursor: pointer;
}

.action-btn {
    font-size: 2.3rem;
    color: #35373f;
    background-color: transparent;
    padding: 4px 13px;
    cursor: pointer;
}

.user-avartar {
    width: 32px;
    height: 100%;
    border-radius: 50%;
    margin-left: 13px;
}
